<template>
  <div class="product">
    <router-link :to="'/product/'+info.id" class="product-main">
        <img :src="info.image" alt="无图">
        <h4>{{ info.name }}</h4>
        <div class="product-color" :style="{ background:colors[info.color]}"></div>
        <div class="product-cost">￥{{ info.cost }}</div>
        <div class="product-add-cart" @click.prevent="handleCart(info.id)">加入购物车</div>
    </router-link>
  </div>
</template>

<script>
export default{
  props:{
    info:{
      type:Object
    }
  },
  data(){
    return{
      colors:{
        '白色':'#ffffff',
        '金色':'#dac272',
        '蓝色':'#233472',
        '红色':'#f2352e'
      }
    }
  },
  methods:{
    handleCart(id){
      this.$store.commit('addCart',id);
    }
  }

}
</script>

<style scoped lang="less">
a{
  text-decoration: none;
}
.product{
  width: 20%; float: left;
  &-main{
    display: block;
    margin: 16px; padding: 16px;
    border: 1px solid #dddee1; border-radius: 6px;  text-align: center;
    position: relative;
    & img{
      height: 200px;
    }
  }
  h4{
    color: #222;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &:hover h4{
    color: #0070c9;
  }

  &-color{
    width: 16px; height: 16px;
    border-radius: 50%;
    margin: 0 auto;
    border: 1px dotted #dddee1;
  }
  &-cost{
    color:#de4037;
    margin-top: 6px;
  }
  &-add-cart{
    display: none;
    padding: 2px 6px;
    background: #2d8cf0;
    color: #fff;
    font-size: 12px;
    border-radius: 4px;
    cursor: pointer;
    position: absolute;  top:5px; right: 5px;
    height: 30px;   line-height: 30px;
  }
  &:hover &-add-cart{
    display: inline-block;
  }
}
</style>
