<template>
  <div>
    <!-- 控制框 -->
    <div class="list-control">
      <div class="list-control-filter">
          <span>品牌：</span>
          <span v-for="item in brands" class="list-control-filter-item" :class="{on:item===filterBrand}" @click="handleFilterBrand(item)">
            {{ item }}
          </span>
      </div>
      <div class="list-control-filter">
          <span>颜色：</span>
          <span v-for="item in colors" class="list-control-filter-item" :class="{on:item===filterColor}" @click="handleFilterColor(item)">
            {{ item }}
          </span>
      </div>
      <div class="list-control-order">
          <span>排序：</span>
          <span class="list-control-order-item" :class="{on:order===''}" @click="handleOrderDefault">
            默认
          </span>
          <span class="list-control-order-item" :class="{on:order==='sales'}" @click="handleOrderSales">
            销售
            <template v-if="order==='sales'">↓</template>
          </span>
          <span class="list-control-order-item" :class="{on:order.indexOf('cost')>-1}" @click="handleOrderCost">
            价格
            <template v-if="order==='cost-asc'">↑</template>
            <template v-if="order==='cost-desc'">↓</template>
          </span>
      </div>
    </div>

    <div class="product-component">
      <Product
        v-for="item in filteredAndOrderedList" :key="item.id"
        :info="item"
      ></Product>
    </div>
    <div class="product-not-found" v-show="!filteredAndOrderedList.length">
      暂无相关商品
    </div>


  </div>
</template>

<script>
import Product from '../../components/ShoppingCart/ShoppingProduct.vue'
export default{
  computed:{
    list(){
      return this.$store.state.productList;
    },
    brands(){
      return this.$store.getters.brands;
    },
    colors(){
      return this.$store.getters.colors;
    },
    filteredAndOrderedList(){
      // 复制原始数据
      let list=[...this.list];
      // 按品牌过滤
      if(this.filterBrand!==''){
        list=list.filter(item=>item.brand==this.filterBrand);
      }
      // 按颜色过滤
      if(this.filterColor!==''){
        list=list.filter(item=>item.color==this.filterColor);
      }
      // 排序
      if(this.order!==''){
        if(this.order==='sales'){
          list=list.sort((a,b)=>b.sales-a.sales);
        }else if(this.order==='cost-desc'){
          list=list.sort((a,b)=>b.cost-a.cost);
        }else if(this.order==='cost-asc'){
          list=list.sort((a,b)=>a.cost-b.cost);
        }
      }
      return list;
    }
  },
  methods:{
    // 筛选品牌
    handleFilterBrand(brand){
      // if(this.filterBrand==''){
      //   this.filterBrand=item;
      // }else{
      //   this.filterBrand='';
      // }
      // 单次点击选中，再次点击取消选中
      if(this.filterBrand===brand){
        this.filterBrand='';
      }else{
        this.filterBrand=brand;
      }
    },
    handleFilterColor(color){
      if(this.filterColor===color){
        this.filterColor='';
      }else{
        this.filterColor=color;
      }
    },
    handleOrderDefault(){
      this.order='';
    },
    handleOrderSales(){
      this.order='sales';
    },
    handleOrderCost(){
      if(this.order==='cost-desc'){
        this.order='cost-asc';
      }else{
        this.order='cost-desc'
      }
    }


  },
  mounted() {
    this.$store.dispatch('getProductList');
  },
  data(){
    return{
      filterBrand:'',
      filterColor:'',
      order:''
    }
  },
  components:{
    Product
  }

}
</script>

<style scoped lang="less">
.list-control{
  margin: 20px; padding: 20px;
  border-radius: 6px;  border: 1px solid #E5E9EF;
  box-shadow: 0 1px 1px rgba(0,0,0,.3);
  color: #000;
}
.list-control-filter-item,.list-control-order-item{
  border: 1px solid #e9eaec;
  margin: 2px;padding: 4px;
  text-align: center;
  border-radius: 6px;
  cursor: pointer;
}
.list-control-filter-item.on,.list-control-order-item.on{
  background: orangered;
  color: #fff;
}
.product-component{
  margin: 20px;

}

</style>
